import { useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";
import useProfileService from "../../api/services/useProfileService";
import { NotificationType } from "../../types/system";

export default function Connect() {
  const { connect } = useProfileService();
  const { profileId } = useParams();
  const navigate = useNavigate();

  // TODO: Hide url on landing to prevent stealing connect id

  useEffect(() => {}, []);

  useEffect(() => {
    const connectWithProfile = async () => {
      try {
        const response = await connect(profileId);

        if (response.connection.connected) {
          navigate(`/profile/${response.connection.accountId}`, {
            state: {
              notification: {
                title: `Clink! You just connected.`,
                type: NotificationType.Success,
                content: "",
              },
            },
          });
        } else {
          navigate(`/profile/${response.connection.accountId}`);
        }
      } catch (error) {
        navigate(`/error/404`);
      }
    };

    connectWithProfile();
  }, [profileId]);

  return (
    <div className="p-4">
      <div className="container">Connecting....</div>
    </div>
  );
}
