import {
  Anchor,
  Avatar,
  Card,
  Container,
  Group,
  Loader,
  Text,
} from "@mantine/core";
import { IconCheck, IconExternalLink, IconX } from "@tabler/icons";
import { useAtom } from "jotai";
import { useEffect, useState } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import useAccountService from "../../api/services/useAccountService";
import { user as userAtom } from "../../store/account";
import { NotificationType } from "../../types/system";

const VerifyAccount = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [isSuccess, setIsSuccess] = useState(false);

  const navigate = useNavigate();
  const [searchParams, setSearchParams] = useSearchParams();

  const { verifyEmail } = useAccountService();

  const [user] = useAtom(userAtom);

  useEffect(() => {
    const verifyEmailService = async (token) => {
      try {
        await verifyEmail(token);
        setIsSuccess(true);
        setTimeout(() => {
          navigate("/");
        }, 1000);
      } catch (error) {
        setIsSuccess(false);
      } finally {
        setIsLoading(false);
      }
    };

    if (searchParams.has("token")) {
      const token = searchParams.get("token");
      searchParams.delete("token");
      setSearchParams(searchParams);

      verifyEmailService(token);
    } else {
      if (user) {
        navigate("/", {
          state: {
            notification: {
              type: NotificationType.Success,
              title: "Nice to know it's really you!",
              content: "You successfuly verified your email address.",
            },
          },
        });
      } else {
        navigate("/account/login", {
          state: {
            notification: {
              type: NotificationType.Success,
              title: "Verified!",
              content:
                "You successfuly verified your email address. Login with your email address and password.",
            },
          },
        });
      }
    }
  }, []);

  return (
    <Container size="xs" my="xl">
      <Card
        withBorder
        radius="md"
        p="xl"
        sx={(theme) => ({
          backgroundColor:
            theme.colorScheme === "dark" ? theme.colors.dark[7] : theme.white,
        })}
      >
        <Group position="left" my="xl">
          {isLoading ? (
            <>
              <Loader />
              <Text size="xl" weight={500}>
                Verifying account...
              </Text>
            </>
          ) : isSuccess ? (
            <>
              <Avatar color="green" radius="xl">
                <IconCheck size={24} />
              </Avatar>
              <Text size="xl" weight={500} color="green">
                Verified!
              </Text>
            </>
          ) : (
            <>
              <Avatar color="red" radius="xl">
                <IconX size={24} />
              </Avatar>
              <Text size="xl" weight={500} color="red">
                Oh no, something happened!
              </Text>
            </>
          )}
        </Group>
        {!isLoading && isSuccess && <Text align="center">Redirecting...</Text>}
        {!isLoading && !isSuccess && (
          <Anchor
            align="center"
            component="button"
            type="button"
            variant="link"
            color="dimmed"
            onClick={() => {
              navigate("/help");
            }}
          >
            <Group align="center">
              <Text>Check our help page</Text>
              <IconExternalLink size="16" />
            </Group>
          </Anchor>
        )}
      </Card>
    </Container>
  );
};

export default VerifyAccount;
