import { useAtom } from "jotai";
import { user as userAtom } from "../../store/account";
import { profile as profileAtom } from "../../store/profile";
import fetchWrapper from "../config/fetchWrapper";
import useAccountService from "./useAccountService";

export default function useProfileService() {
  const baseUrl = `${process.env.REACT_APP_API_URL}/profile`;

  const [profile, setProfile] = useAtom(profileAtom);
  const [user] = useAtom(userAtom);

  const { logout } = useAccountService();

  function getPublicProfile(id) {
    return fetchWrapper.get(`${baseUrl}/${id}`).then((profile) => {
      return profile;
    });
  }

  function getPrivateProfile() {
    return fetchWrapper
      .get(`${baseUrl}`, user.jwtToken, logout)
      .then((profile) => {
        setProfile(profile);
        return profile;
      });
  }

  function createLink(linkObject) {
    return fetchWrapper
      .post(`${baseUrl}/link`, linkObject, user.jwtToken, logout)
      .then((link) => {
        setProfile({
          ...profile,
          links: [...profile.links, link],
        });

        return link;
      });
  }

  function updateLink(linkObject) {
    return fetchWrapper
      .put(`${baseUrl}/link`, linkObject, user.jwtToken, logout)
      .then((link) => {
        // TODO Doesn't update status properly
        const copyLinks = JSON.parse(JSON.stringify(profile.links));
        const edited = copyLinks.find((l) => l.id === link.id);
        edited.platform = link.platform;
        edited.url = link.url;
        edited.active = link.active;

        setProfile({
          ...profile,
          links: copyLinks,
        });

        return link;
      });
  }

  function deleteLink(id) {
    return fetchWrapper
      .delete(`${baseUrl}/link`, { id }, user.jwtToken, logout)
      .then((response) => {
        const filtered = profile.links.filter((l) => l._id !== id);

        setProfile({
          ...profile,
          links: JSON.parse(JSON.stringify(filtered)),
        });

        return response;
      });
  }

  function connect(profileId) {
    return fetchWrapper
      .get(`${baseUrl}/connect/${profileId}`, user?.jwtToken, logout)
      .then((response) => {
        return response;
      });
  }

  return {
    getPublicProfile,
    getPrivateProfile,
    createLink,
    updateLink,
    deleteLink,
    connect,
  };
}
