export const urlValidator = (value) =>
  /^(http|https):\/\/[^ "]+$/.test(value)
    ? null
    : "Invalid URL. Make sure you include the whole url.";

export const linkPlatformNameValidator = (value) =>
  value.length > 0 && value.length <= 40
    ? null
    : "Required field. Maximum 40 characters.";

export const passwordValidator = (value) =>
  value.length < 6 ? "Password should include at least 6 characters" : null;

export const confirmPasswordValidator = (value, password) =>
  value !== password ? "Passwords did not match" : null;

export const emailValidator = (value) =>
  /^\S+@\S+$/.test(value) ? null : "Invalid email";
