import {
  Button,
  Container,
  Divider,
  Paper,
  PasswordInput,
  Stack,
  Text,
} from "@mantine/core";
import { useForm } from "@mantine/form";
import { useAtom } from "jotai";
import { useEffect, useState } from "react";
import { Navigate, useNavigate, useSearchParams } from "react-router-dom";
import useAccountService from "../../api/services/useAccountService";
import Notification from "../../components/Notification";
import { user as userAtom } from "../../store/account";
import { NotificationType } from "../../types/system";
import {
  confirmPasswordValidator,
  passwordValidator,
} from "../../utils/validators";

export default function ResetPassword() {
  const [notification, setNotification] = useState(null);
  const [token, setToken] = useState(null);

  const [searchParams, setSearchParams] = useSearchParams();

  const navigate = useNavigate();

  useEffect(() => {
    if (searchParams.has("token")) {
      setToken(searchParams.get("token"));
      searchParams.delete("token");
      setSearchParams(searchParams);
    } else {
      navigate("/account/login");
    }
  }, []);

  const form = useForm({
    initialValues: {
      password: "",
      confirmPassword: "",
    },

    validate: {
      password: passwordValidator,
      confirmPassword: (value, values) =>
        confirmPasswordValidator(value, values.password),
    },

    validateInputOnChange: true,
  });

  const { resetPassword: resetPasswordService } = useAccountService();

  const [user] = useAtom(userAtom);

  const resetPassword = async ({ password, confirmPassword }) => {
    try {
      await resetPasswordService({ token, password, confirmPassword });
      navigate("/account/login", {
        state: {
          notification: {
            title: "You changed your password!",
            type: NotificationType.Success,
            content: "Enter your email and your new password to login.",
          },
        },
      });
    } catch (error) {
      setNotification({
        title: "Something happened!",
        type: NotificationType.Error,
        content: "Please try entering your new password again.",
      });
    }
  };

  if (user) return <Navigate to="/" />;

  return (
    <Container size="xs">
      <Paper radius="md" p="xl" m="xl" withBorder>
        {notification && (
          <Notification
            type={notification.type}
            title={notification.title}
            onClose={() => setNotification(null)}
          >
            {notification.content}
          </Notification>
        )}

        <Text size="lg" weight={500}>
          Your new password
        </Text>

        <Divider labelPosition="center" my="lg" />

        <Text my="md" size="sm">
          Please enter a secure password and don't share it with others.
        </Text>

        <form onSubmit={form.onSubmit(resetPassword)}>
          <Stack>
            <PasswordInput
              required
              placeholder="Your password"
              {...form.getInputProps("password")}
            />

            <PasswordInput
              required
              placeholder="Repeat password"
              {...form.getInputProps("confirmPassword")}
            />
          </Stack>

          <Button my="lg" type="submit">
            Reset password
          </Button>
        </form>
      </Paper>
    </Container>
  );
}
