import {
  Anchor,
  Button,
  Card,
  createStyles,
  Grid,
  Group,
  PasswordInput,
  Stack,
  Text
} from "@mantine/core";
import { useForm } from "@mantine/form";
import { showNotification } from "@mantine/notifications";

import { IconLock, IconPencil } from "@tabler/icons";
import { useAtom } from "jotai";
import { useState } from "react";
import useAccountService from "../../api/services/useAccountService";
import { user as userAtom } from "../../store/account";
import {
  confirmPasswordValidator,
  passwordValidator
} from "../../utils/validators";

const useStyles = createStyles((theme) => ({
  card: {
    backgroundColor:
      theme.colorScheme === "dark"
        ? theme.colors.dark[6]
        : theme.colors.gray[0],
  },
}));

const PasswordCard = () => {
  const { classes } = useStyles();

  const [user] = useAtom(userAtom);
  const { update: updateService } = useAccountService();
  const [isEditMode, setIsEditMode] = useState(false);

  const form = useForm({
    initialValues: {
      password: "",
      confirmPassword: "",
    },

    validate: {
      password: passwordValidator,
      confirmPassword: (value, values) =>
        confirmPasswordValidator(value, values.password),
    },

    validateInputOnChange: true,
  });

  const update = async (values) => {
    try {
      const valuesTrimmed = {};
      Object.keys(values).forEach(
        (key) => (valuesTrimmed[key] = values[key].trim())
      );
      form.setValues(valuesTrimmed);

      await updateService(user.id, values);
      showNotification({
        title: "Success",
        message: "You've updated your password!",
        color: "green",
      });
      setIsEditMode(false);
    } catch (error) {
      showNotification({
        title: "Uh oh!",
        message: "Something happened. Please try again.",
        color: "red",
      });
    }
  };

  const reset = () => {
    setIsEditMode(false);
    form.reset();
  };

  return (
    <Grid mb="lg">
      {!isEditMode && (
        <Grid.Col xs={12}>
          <Group position="right">
            <Anchor
              component="button"
              type="button"
              gradient
              onClick={() => setIsEditMode(true)}
            >
              <IconPencil size={16} /> Change password
            </Anchor>
          </Group>
        </Grid.Col>
      )}
      <Grid.Col xs={12}>
        <Card withBorder radius="md" p={"xl"} className={classes.card}>
          <Grid.Col xs={12}>
            {isEditMode ? (
              <form onSubmit={form.onSubmit(update)} onReset={reset}>
                <Text mb="lg" size="xs" color="dimmed" align="center">
                  <IconLock size="14" /> Choose a secure password and don't
                  share it with anyone.
                </Text>
                <Stack>
                  <PasswordInput
                    required
                    placeholder="Your new password"
                    {...form.getInputProps("password")}
                  />
                  <PasswordInput
                    required
                    placeholder="Repeat new password"
                    {...form.getInputProps("confirmPassword")}
                  />
                </Stack>
                <Group position="right" mt="lg">
                  <Button type="reset" variant="subtle">
                    Cancel
                  </Button>
                  <Button type="submit">Update</Button>
                </Group>
              </form>
            ) : (
              <>
                <Text
                  size="xs"
                  sx={{ textTransform: "uppercase" }}
                  weight={700}
                  color="dimmed"
                >
                  Password
                </Text>
                <Text>*****</Text>
              </>
            )}
          </Grid.Col>
        </Card>
      </Grid.Col>
    </Grid>
  );
};

export default PasswordCard;
