import { useAtom } from "jotai";
import { user as userAtom } from "../../store/account";
import { redirect } from "react-router-dom";
import fetchWrapper from "../config/fetchWrapper";

export default function useAccountService() {
  const baseUrl = `${process.env.REACT_APP_API_URL}/accounts`;

  const [user, setUser] = useAtom(userAtom);

  let refreshTokenTimeout = null;

  const startRefreshTokenTimer = (token) => {
    // parse json object from base64 encoded jwt token
    const jwtToken = JSON.parse(atob(token.split(".")[1]));

    // set a timeout to refresh the token a minute before it expires
    const expires = new Date(jwtToken.exp * 1000);
    const timeout = expires.getTime() - Date.now() - 60 * 1000;
    refreshTokenTimeout = setTimeout(refreshToken, timeout);
  };

  const stopRefreshTokenTimer = () =>
    refreshTokenTimeout ? clearTimeout(refreshTokenTimeout) : null;

  const login = (email, password) =>
    fetchWrapper
      .post(`${baseUrl}/authenticate`, { email, password })
      .then((user) => {
        setUser(user);
        startRefreshTokenTimer(user.jwtToken);
        return user;
      });

  const logout = () => {
    // revoke token, stop refresh timer, set user to null and redirect to login page
    fetchWrapper.post(`${baseUrl}/revoke-token`, {}, user.jwtToken);
    stopRefreshTokenTimer();
    setUser(null);

    redirect("/account/login");
  };

  const refreshToken = () =>
    fetchWrapper
      .post(`${baseUrl}/refresh-token`, {}, user?.jwtToken, logout)
      .then((user) => {
        setUser(user);
        startRefreshTokenTimer(user.jwtToken);
        return user;
      })
      .catch((error) => {
        return error;
      });

  const register = (params) => fetchWrapper.post(`${baseUrl}/register`, params);

  const verifyEmail = (token) =>
    fetchWrapper.post(`${baseUrl}/verify-email`, { token });

  const forgotPassword = (email) =>
    fetchWrapper.post(`${baseUrl}/forgot-password`, { email });

  const validateResetToken = (token) =>
    fetchWrapper.post(
      `${baseUrl}/validate-reset-token`,
      { token },
      user.jwtToken,
      logout
    );

  const resetPassword = ({ token, password, confirmPassword }) =>
    fetchWrapper.post(`${baseUrl}/reset-password`, {
      token,
      password,
      confirmPassword,
    });

  const getAll = () => fetchWrapper.get(baseUrl, user.jwtToken, logout);

  const getById = (id) =>
    fetchWrapper.get(`${baseUrl}/${id}`, user.jwtToken, logout);

  const create = (params) =>
    fetchWrapper.post(baseUrl, params, user.jwtToken, logout);

  const update = (id, params) =>
    fetchWrapper
      .put(`${baseUrl}/${id}`, params, user.jwtToken, logout)
      .then((userResponse) => {
        // update stored user if the logged in user updated their own record
        if (userResponse.id === user.id) {
          setUser({ ...user, ...userResponse });
        }
        return user;
      });

  // prefixed with underscore because 'delete' is a reserved word in javascript
  const _delete = (id) =>
    fetchWrapper
      .delete(`${baseUrl}/${id}`, user.jwtToken, logout)
      .then((response) => {
        // auto logout if the logged in user deleted their own record
        if (id === user.id) {
          logout();
        }
        return response;
      });

  return {
    login,
    logout,
    refreshToken,
    register,
    verifyEmail,
    forgotPassword,
    validateResetToken,
    resetPassword,
    getAll,
    getById,
    create,
    update,
    delete: _delete,
  };
}
