import {
  Anchor,
  Button,
  Divider,
  Grid,
  Group,
  Paper,
  Spoiler,
  Text,
} from "@mantine/core";
import { IconUserPlus, IconWorld } from "@tabler/icons";
import { useEffect, useState } from "react";
import { useLocation, useNavigate, useParams } from "react-router-dom";

import useProfileService from "../../api/services/useProfileService";
import CardLink from "../../components/links/CardLink";
import ProfileSkeleton from "../../components/loaders/ProfileSkeleton";
import Notification from "../../components/Notification";
import PersonalInformation from "../../components/PersonalInformation";
import ProfileAbout from "../../components/ProfileAbout";

function PublicProfile() {
  const [profile, setProfile] = useState(null);
  const { userId } = useParams();
  const navigate = useNavigate();
  const location = useLocation();

  const [notification, setNotification] = useState(false);

  const { getPublicProfile: getPublicProfileService } = useProfileService();

  useEffect(() => {
    const getPublicProfile = async () => {
      try {
        const profile = await getPublicProfileService(userId);
        setProfile(profile);

        if (location.state?.notification) {
          setNotification({
            ...location.state.notification,
            content: (
              <Text>
                Find your new connection in{" "}
                <Anchor
                  component="button"
                  type="button"
                  onClick={() => {
                    navigate("/");
                  }}
                >
                  Connections
                </Anchor>
              </Text>
            ),
          });
        }
      } catch (error) {
        navigate("/error/404");
      }
    };

    getPublicProfile();
  }, [userId]);

  if (!profile) {
    return <ProfileSkeleton />;
  }

  // TODO Add time of connection if connected.
  return (
    <>
      <PersonalInformation profile={profile} />

      <Spoiler mt={"md"} maxHeight={0} showLabel="About me" hideLabel="Hide">
        <ProfileAbout mb="xs">{profile?.description}</ProfileAbout>
      </Spoiler>

      <Group mt={"lg"}>
        <Button variant="light" size="md" leftIcon={<IconUserPlus size={16} />}>
          Save to contacts
        </Button>
      </Group>

      <Divider
        my="lg"
        labelProps={{ color: "dimmed" }}
        label="Connect"
        labelPosition="center"
      />

      {notification && (
        <Notification
          type={notification.type}
          title={notification.title}
          onClose={() => setNotification(null)}
        >
          {notification.content}
        </Notification>
      )}

      <Paper radius="md" py="xs">
        <Grid mt="sm">
          {profile?.links.length ? (
            profile.links.map((link) => (
              <Grid.Col xs={12} key={link.platform}>
                <CardLink link={link} Icon={IconWorld} />
              </Grid.Col>
            ))
          ) : (
            <Grid.Col xs={12} mt="md" px="lg">
              <Text align="center" color="dimmed" weight={500} size="lg">
                {profile.account.firstName} didn't add links yet.
              </Text>
            </Grid.Col>
          )}
        </Grid>
      </Paper>
    </>
  );
}

export default PublicProfile;
