import { Button, Group, Paper, Switch, TextInput } from "@mantine/core";

import { useForm } from "@mantine/form";
import { showNotification } from "@mantine/notifications";
import useProfileService from "../../api/services/useProfileService";
import {
  linkPlatformNameValidator,
  urlValidator,
} from "../../utils/validators";
import LinkTypeSelect from "./LinkTypeSelect";

const CardCreateLink = ({ onFinish }) => {
  const { createLink } = useProfileService();

  const form = useForm({
    initialValues: {
      platform: "",
      url: "",
      active: true,
    },

    validate: {
      url: urlValidator,
      platform: linkPlatformNameValidator,
    },
  });

  const submitLink = (values) => {
    createLink(values).then(() => {
      onFinish();
      form.reset();

      showNotification({
        title: "Success",
        message: "You've added a link!",
        color: "green",
      });
    });
  };

  const cancel = () => {
    onFinish();
    form.reset();
  };

  return (
    <Paper radius="md" p="xl" mt="sm" withBorder>
      <form onSubmit={form.onSubmit(submitLink)} onReset={cancel}>
        <LinkTypeSelect
          withAsterisk
          formInputProps={form.getInputProps("platform")}
        />

        <TextInput
          withAsterisk
          label="URL"
          placeholder="e.g. https://www.linkedin.com/in/walter-white/"
          mt="md"
          {...form.getInputProps("url")}
        />

        <Switch
          label="Display this link on my public profile"
          mt="md"
          {...form.getInputProps("active", { type: "checkbox" })}
        />

        <Group position="right" mt="xl">
          <Button variant="outline" type="reset">
            Cancel
          </Button>
          <Button variant="filled" type="submit">
            Submit
          </Button>
        </Group>
      </form>
    </Paper>
  );
};

export default CardCreateLink;
