import React, { useEffect, useState } from "react";

import { Center, createStyles, SegmentedControl } from "@mantine/core";
import {
  IconHome,
  IconQrcode,
  IconSettings,
  IconUserCircle,
} from "@tabler/icons";
import { useAtom } from "jotai";
import { useLocation, useNavigate } from "react-router-dom";
import { user as userAtom } from "../../store/account";

const useStyles = createStyles((theme) => ({
  controlRoot: {
    position: "fixed",
    left: "16px",
    right: "16px",
    bottom: "16px",
    backgroundColor:
      theme.colorScheme === "dark"
        ? theme.colors.gray[7]
        : theme.colors.gray[4],
    boxShadow: theme.shadows.md,

    [theme.fn.largerThan("xs")]: {
      display: "none",
    },
  },

  //
}));

function MobileNav() {
  const [user] = useAtom(userAtom);
  const [navValue, setNavValue] = useState("Home");

  const { classes } = useStyles();

  const location = useLocation();
  const navigate = useNavigate();

  useEffect(() => {
    let value = null;
    switch (location.pathname) {
      case "/":
        value = "Home";
        break;

      case "/profile":
        value = "Profile";
        break;

      case "/profile/share":
        value = "QR";
        break;

      case "/settings":
        value = "Settings";
        break;

      default:
        break;
    }

    setNavValue(value);
  });

  const navigateTo = (location) => {
    if (location !== location.pathname) {
      navigate(location);
    }
  };

  const handleNavChange = (value) => {
    switch (value) {
      case "Home":
        navigateTo("/");
        break;

      case "Profile":
        navigateTo("/profile");
        break;

      case "QR":
        navigateTo("/profile/share");
        break;

      case "Settings":
        navigateTo("/settings");
        break;

      default:
        break;
    }
  };

  // only show nav when logged in
  if (!user) return null;

  return (
    <SegmentedControl
      sx={{
        zIndex: 2,
      }}
      value={navValue}
      onChange={handleNavChange}
      classNames={{
        root: classes.controlRoot,
      }}
      radius="xl"
      data={[
        {
          value: "Home",
          label: (
            <Center>
              <IconHome size={24} />
            </Center>
          ),
        },
        {
          value: "Profile",
          label: (
            <Center>
              <IconUserCircle size={24} />
            </Center>
          ),
        },
        {
          value: "QR",
          label: (
            <Center>
              <IconQrcode size={24} />
            </Center>
          ),
        },
        {
          value: "Settings",
          label: (
            <Center>
              <IconSettings size={24} />
            </Center>
          ),
        },
      ]}
    />
  );
}

export default MobileNav;
