import { Alert } from "@mantine/core";
import { IconAlertCircle, IconCheck, IconInfoCircle } from "@tabler/icons";
import { useEffect, useState } from "react";
import { NotificationType } from "../types/system";

export default function Notification({
  type = NotificationType.Info,
  title,
  withCloseButton = true,
  onClose,
  children,
}) {
  const [options, setOptions] = useState({});

  useEffect(() => {
    switch (type) {
      case NotificationType.Info:
        setOptions({ color: "blue", icon: <IconInfoCircle size={16} /> });
        break;
      case NotificationType.Warn:
        setOptions({ color: "orange", icon: <IconAlertCircle size={16} /> });
        break;
      case NotificationType.Error:
        setOptions({ color: "red", icon: <IconAlertCircle size={16} /> });
        break;
      case NotificationType.Success:
        setOptions({ color: "green", icon: <IconCheck size={16} /> });
        break;

      default:
        break;
    }
  }, [type]);

  return (
    <Alert
      icon={options.icon}
      title={title}
      color={options.color}
      radius="md"
      withCloseButton={withCloseButton}
      variant="outline"
      my={"sm"}
      onClose={onClose}
    >
      {children}
    </Alert>
  );
}
