import {
  Anchor,
  Button,
  Card,
  createStyles,
  Grid,
  Group,
  Text,
  TextInput,
  Title
} from "@mantine/core";
import { useForm } from "@mantine/form";
import { showNotification } from "@mantine/notifications";

import { IconAt, IconPencil } from "@tabler/icons";
import { useAtom } from "jotai";
import { useState } from "react";
import useAccountService from "../../api/services/useAccountService";
import { user as userAtom } from "../../store/account";
import { emailValidator } from "../../utils/validators";

const useStyles = createStyles((theme) => ({
  card: {
    backgroundColor:
      theme.colorScheme === "dark"
        ? theme.colors.dark[6]
        : theme.colors.gray[0],
  },
}));

const BasicInformationCard = () => {
  const { classes } = useStyles();

  const [user] = useAtom(userAtom);
  const { update: updateService } = useAccountService();
  const [isEditMode, setIsEditMode] = useState(false);

  const form = useForm({
    initialValues: {
      firstName: user.firstName,
      lastName: user.lastName,
      location: "",
      email: user.email,
      handle: "",
    },

    validate: {
      email: emailValidator,
    },

    validateInputOnChange: true,
  });

  const update = async (values) => {
    try {
      const valuesTrimmed = {};
      Object.keys(values).forEach(
        (key) => (valuesTrimmed[key] = values[key].trim())
      );
      form.setValues(valuesTrimmed);

      await updateService(user.id, values);
      showNotification({
        title: "Success",
        message: "You've updated your basic information!",
        color: "green",
      });
      setIsEditMode(false);
    } catch (error) {
      showNotification({
        title: "Uh oh!",
        message: "Something happened. Please try again.",
        color: "red",
      });
    }
  };

  const reset = () => {
    setIsEditMode(false);
    form.reset();
  };

  const content = isEditMode ? (
    <form onSubmit={form.onSubmit(update)} onReset={reset}>
      <Grid>
        <Grid.Col xs={12}>
          <Text
            size="xs"
            sx={{ textTransform: "uppercase" }}
            weight={700}
            color="dimmed"
          >
            Name
          </Text>
          <Group mt={"sm"} grow>
            <TextInput
              required
              placeholder="First name"
              {...form.getInputProps("firstName")}
            />
            <TextInput
              required
              placeholder="Last name"
              {...form.getInputProps("lastName")}
            />
          </Group>
        </Grid.Col>
        <Grid.Col xs={12}>
          <Text
            size="xs"
            sx={{ textTransform: "uppercase" }}
            weight={700}
            color="dimmed"
          >
            Location
          </Text>
          <TextInput
            mt={"sm"}
            placeholder="Amsterdam, Netherlands"
            {...form.getInputProps("location")}
          />
        </Grid.Col>
        <Grid.Col xs={6}>
          <Text
            size="xs"
            sx={{ textTransform: "uppercase" }}
            weight={700}
            color="dimmed"
          >
            Email
          </Text>
          <TextInput
            mt={"sm"}
            placeholder="Your new email address"
            {...form.getInputProps("email")}
          />
        </Grid.Col>
        <Grid.Col xs={6}>
          <Text
            size="xs"
            sx={{ textTransform: "uppercase" }}
            weight={700}
            color="dimmed"
          >
            Handle
          </Text>
          <TextInput
            mt={"sm"}
            icon={<IconAt size={14} />}
            placeholder="Your new handle"
            {...form.getInputProps("handle")}
          />
        </Grid.Col>

        <Grid.Col xs={12} mt="md">
          <Group position="right">
            <Button type="reset" variant="subtle">
              Cancel
            </Button>
            <Button type="submit">Update</Button>
          </Group>
        </Grid.Col>
      </Grid>
    </form>
  ) : (
    <Grid>
      <Grid.Col xs={6}>
        <Text
          size="xs"
          sx={{ textTransform: "uppercase" }}
          weight={700}
          color="dimmed"
        >
          Name
        </Text>
        <Text>
          {user.firstName} {user.lastName}
        </Text>
      </Grid.Col>
      <Grid.Col xs={6}>
        <Text
          size="xs"
          sx={{ textTransform: "uppercase" }}
          weight={700}
          color="dimmed"
        >
          Location
        </Text>
        <Text>{user.location}</Text>
      </Grid.Col>
      <Grid.Col xs={6}>
        <Text
          size="xs"
          sx={{ textTransform: "uppercase" }}
          weight={700}
          color="dimmed"
        >
          Email
        </Text>
        <Text>{user.email}</Text>
      </Grid.Col>
      <Grid.Col xs={6}>
        <Text
          size="xs"
          sx={{ textTransform: "uppercase" }}
          weight={700}
          color="dimmed"
        >
          Handle
        </Text>
        <Text>{user.handle}</Text>
      </Grid.Col>
    </Grid>
  );

  return (
    <Grid my="lg">
      <Grid.Col xs={12}>
        <Group position="apart">
          <Title size={24} weight={500}>
            Basic information
          </Title>

          {!isEditMode && (
            <Anchor
              component="button"
              type="button"
              gradient
              onClick={() => setIsEditMode(true)}
            >
              <IconPencil size={16} /> Edit
            </Anchor>
          )}
        </Group>
      </Grid.Col>
      <Grid.Col xs={12}>
        <Card withBorder radius="md" p={"xl"} className={classes.card}>
          {content}
        </Card>
      </Grid.Col>
    </Grid>
  );
};

export default BasicInformationCard;
