import React, { useState } from "react";

import {
  Button,
  Card,
  createStyles,
  Group,
  Modal,
  Switch,
  Text,
  TextInput,
  UnstyledButton,
} from "@mantine/core";

import { useForm } from "@mantine/form";
import { showNotification } from "@mantine/notifications";
import useProfileService from "../../api/services/useProfileService";
import {
  linkPlatformNameValidator,
  urlValidator,
} from "../../utils/validators";
import LinkTypeSelect from "./LinkTypeSelect";

const useStyles = createStyles((theme) => ({
  card: {
    border: `1px solid ${
      theme.colorScheme === "dark" ? theme.colors.dark[5] : theme.colors.gray[1]
    }`,
    borderTop: `5px solid ${theme.fn.primaryColor()}`,
  },

  textLink: {
    color:
      theme.colorScheme === "dark"
        ? theme.colors.dark[3]
        : theme.colors.gray[5],
  },
}));

const CardEditLink = ({ link, onCancel }) => {
  const { classes } = useStyles();
  const { updateLink, deleteLink } = useProfileService();

  const [isDeletePromptVisible, setIsDeletePromptVisible] = useState();

  const form = useForm({
    initialValues: {
      platform: link.platform,
      url: link.url,
      active: link.active,
    },

    validate: {
      url: urlValidator,
      platform: linkPlatformNameValidator,
    },
  });

  const submitLink = (values) => {
    updateLink({ id: link._id, ...values }).then(() => {
      showNotification({
        title: "Success",
        message: "You've updated the link!",
        color: "green",
      });
      form.reset();
      onCancel();
    });
  };

  const cancel = () => {
    form.reset();
    onCancel();
  };

  const confirmDelete = () => {
    deleteLink(link._id).then(() => {
      setIsDeletePromptVisible(false);
      showNotification({
        title: "Success",
        message: "You've deleted the link!",
        color: "green",
      });
    });
  };

  return (
    <>
      <Modal
        centered
        opened={isDeletePromptVisible}
        onClose={() => setIsDeletePromptVisible(false)}
        title="Are you sure you want to delete this link?"
      >
        <Group position="right" mt="md" spacing={16}>
          <Button variant="outline" onClick={confirmDelete}>
            Yes
          </Button>
          <Button
            variant="filled"
            onClick={() => setIsDeletePromptVisible(false)}
          >
            Cancel
          </Button>
        </Group>
      </Modal>
      <Card shadow="md" radius="md" className={classes.card}>
        <form onSubmit={form.onSubmit(submitLink)} onReset={cancel}>
          <LinkTypeSelect
            withAsterisk
            formInputProps={form.getInputProps("platform")}
          />

          <TextInput
            withAsterisk
            label="URL"
            placeholder="e.g. https://www.linkedin.com/in/walter-white/"
            mt="md"
            {...form.getInputProps("url")}
          />

          <Switch
            label="Active"
            description="Display this link on my public profile"
            mt="md"
            {...form.getInputProps("active", { type: "checkbox" })}
          />

          <Group noWrap>
            <Group position="left" mt="md"></Group>
          </Group>

          <Group noWrap position="apart" mt="md">
            <UnstyledButton
              onClick={() => setIsDeletePromptVisible(true)}
              mr={16}
            >
              <Text size="sm" underline className={classes.textLink}>
                Delete
              </Text>
            </UnstyledButton>
            <Group position="right">
              <Button variant="outline" type="reset">
                Cancel
              </Button>
              <Button variant="filled" type="submit">
                Submit
              </Button>
            </Group>
          </Group>
        </form>
      </Card>
    </>
  );
};

export default CardEditLink;
