import { Box, createStyles, Divider, Group, Skeleton } from "@mantine/core";
import React from "react";

const useStyles = createStyles((theme) => ({
  avatar: {
    boxShadow: `0px 0px 0px 6px ${
      theme.colorScheme === "dark" ? theme.colors.dark[7] : theme.colors.gray[0]
    }`,
  },
}));

const ProfileSkeleton = () => {
  const { classes } = useStyles();

  return (
    <>
      <Box>
        <Skeleton
          pos="absolute"
          top={60}
          left={0}
          right={0}
          height={105}
          radius="lg"
        />
      </Box>
      <Group position="center" mt={40}>
        <Skeleton
          height={94}
          width={94}
          radius="md"
          className={classes.avatar}
        />
      </Group>
      <Box mt="sm">
        <Skeleton height={16} width={"20%"} mb={6} radius="md" />
        <Skeleton height={24} width={"40%"} mb={6} radius="md" />
        <Skeleton height={20} width={"60%"} mb={6} radius="md" />
        <Skeleton height={16} width={"15%"} mb={6} radius="md" />
        <Skeleton height={20} width={"80px"} mt={16} radius="md" />
      </Box>

      <Skeleton height={38} my={32} width={150} radius="md" />

      <Divider />

      <Box mt="xl">
        <Group my={"xs"} position="left">
          <Skeleton circle height={38} mr="lg" />
          <Skeleton height={16} radius="xl" width={190} />
        </Group>
        <Group my={"xs"} position="left">
          <Skeleton circle height={38} mr="lg" />
          <Skeleton height={16} radius="xl" width={170} />
        </Group>
        <Group my={"xs"} position="left">
          <Skeleton circle height={38} mr="lg" />
          <Skeleton height={16} radius="xl" width={200} />
        </Group>
        <Group my={"xs"} position="left">
          <Skeleton circle height={38} mr="lg" />
          <Skeleton height={16} radius="xl" width={210} />
        </Group>
      </Box>
    </>
  );
};

export default ProfileSkeleton;
