import {
  Anchor,
  Button,
  Container,
  Divider,
  Group,
  Paper,
  Stack,
  Text,
  TextInput,
} from "@mantine/core";
import { useForm } from "@mantine/form";
import { useAtom } from "jotai";
import { useState } from "react";
import { Navigate, useNavigate } from "react-router-dom/dist";
import useAccountService from "../../api/services/useAccountService";
import Notification from "../../components/Notification";
import { user as userAtom } from "../../store/account";
import { NotificationType } from "../../types/system";
import { emailValidator } from "../../utils/validators";

const ForgotPassword = () => {
  const [notification, setNotification] = useState(null);

  const { forgotPassword: forgotPasswordService } = useAccountService();
  const navigate = useNavigate();

  const form = useForm({
    initialValues: {
      email: "",
    },

    validate: {
      email: emailValidator,
    },
  });

  const [user] = useAtom(userAtom);

  const forgotPassword = async (values) => {
    try {
      await forgotPasswordService(values.email);
      setNotification({
        title: "We found your account!",
        type: NotificationType.Success,
        content:
          "Please check your email for instructions on how to reset your password.",
      });
    } catch (error) {
      setNotification({
        title: "Aww snap!",
        type: NotificationType.Error,
        content:
          "Something went wrong. We didn't find your account. Are you sure you spelled it out correctly?",
      });
    }
  };

  if (user) return <Navigate to="/" />;

  return (
    <Container size="xs">
      <Paper radius="md" p="xl" m="xl" withBorder>
        {notification && (
          <Notification
            type={notification.type}
            title={notification.title}
            onClose={() => setNotification(null)}
          >
            {notification.content}
          </Notification>
        )}

        <Text size="lg" weight={500}>
          Find your account
        </Text>

        <Divider labelPosition="center" my="lg" />

        <Text my="md" size="sm">
          Please enter your email to reset your password.
        </Text>

        <form onSubmit={form.onSubmit(forgotPassword)}>
          <Stack>
            <TextInput
              required
              placeholder="Your email"
              {...form.getInputProps("email")}
            />
          </Stack>

          <Group position="apart" mt="xl">
            <Anchor
              component="button"
              type="button"
              color="dimmed"
              onClick={() => {
                navigate("/account/login");
              }}
              size="xs"
            >
              Back to login.
            </Anchor>
            <Button type="submit">Submit</Button>
          </Group>
        </form>
      </Paper>
    </Container>
  );
};

export default ForgotPassword;
