function get(url, token, logoutHandler) {
  const requestOptions = {
    method: "GET",
    headers: authHeader(url, token),
  };
  return fetch(url, requestOptions).then((response) =>
    handleResponse(response, token, logoutHandler)
  );
}

function post(url, body, token, logoutHandler) {
  const requestOptions = {
    method: "POST",
    headers: { "Content-Type": "application/json", ...authHeader(url, token) },
    credentials: "include",
    body: JSON.stringify(body),
  };
  return fetch(url, requestOptions).then((response) =>
    handleResponse(response, token, logoutHandler)
  );
}

function put(url, body, token, logoutHandler) {
  const requestOptions = {
    method: "PUT",
    headers: { "Content-Type": "application/json", ...authHeader(url, token) },
    body: JSON.stringify(body),
  };
  return fetch(url, requestOptions).then((response) =>
    handleResponse(response, token, logoutHandler)
  );
}

function _delete(url, body, token, logoutHandler) {
  const requestOptions = {
    method: "DELETE",
    body: JSON.stringify(body),
    headers: { "Content-Type": "application/json", ...authHeader(url, token) },
  };
  return fetch(url, requestOptions).then((response) =>
    handleResponse(response, token, logoutHandler)
  );
}

function authHeader(url, token) {
  // return auth header with jwt if user is logged in and request is to the api url
  const isLoggedIn = !!token;
  const isApiUrl = url.startsWith(process.env.REACT_APP_API_URL);
  if (isLoggedIn && isApiUrl) {
    return { Authorization: `Bearer ${token}` };
  } else {
    return {};
  }
}

function handleResponse(response, token, logoutHandler) {
  return response.text().then((text) => {
    const data = text && JSON.parse(text);

    if (!response.ok) {
      if ([401, 403].includes(response.status) && token) {
        // auto logout if 401 Unauthorized or 403 Forbidden response returned from api
        logoutHandler();
      }

      const error = (data && data.message) || response.statusText;
      return Promise.reject(error);
    }

    return data;
  });
}

const fetchWrapper = {
  get,
  post,
  put,
  delete: _delete,
};

export default fetchWrapper;
