import {
  Anchor,
  Button,
  Container,
  Divider,
  Group,
  Paper,
  PasswordInput,
  Stack,
  Text,
  TextInput,
} from "@mantine/core";
import { useForm } from "@mantine/form";
import { upperFirst, useToggle } from "@mantine/hooks";
import { useAtom } from "jotai";
import { useEffect, useState } from "react";
import { Navigate, useLocation, useNavigate } from "react-router-dom";
import useAccountService from "../../api/services/useAccountService";
import Notification from "../../components/Notification";
import { GoogleButton, LinkedInButton } from "../../components/SocialButtons";
import { user as userAtom } from "../../store/account";
import { NotificationType } from "../../types/system";
import {
  confirmPasswordValidator,
  emailValidator,
  passwordValidator,
} from "../../utils/validators";

export default function Authentication() {
  const [notification, setNotification] = useState(null);

  const [type, toggle] = useToggle(["login", "register"]);
  const form = useForm({
    initialValues: {
      firstName: "",
      lastName: "",
      email: "",
      password: "",
      confirmPassword: "",
    },

    validate: {
      email: emailValidator,
      password: passwordValidator,
      confirmPassword: (value, values) =>
        type === "register"
          ? confirmPasswordValidator(value, values.password)
          : null,
    },

    validateInputOnChange: type === "register",
  });

  const navigate = useNavigate();
  const location = useLocation();

  const { login: loginService, register: registerService } =
    useAccountService();

  const [user] = useAtom(userAtom);

  useEffect(() => {
    if (location.pathname === "/account/register") {
      toggle();
    }
    if (location.state?.notification) {
      setNotification(location.state.notification);
    }
  }, []);

  const register = async (values) => {
    try {
      await registerService({ ...values, title: "Mr.", acceptTerms: true });
      // TODO Make login automatic with signup
      navigate("/", {
        state: {
          notification: {
            type: NotificationType.Success,
            title: `Welcome ${values.firstName}!`,
            content: "Use your email address and password to login.",
          },
        },
      });
    } catch (error) {
      setNotification({ title: error, type: NotificationType.Error });
    }
  };

  const login = (values) => {
    loginService(values.email, values.password)
      .then(() => {
        const { from } = location.state || { from: { pathname: "/" } };
        navigate(from);
      })
      .catch((error) => {
        setNotification({ title: error, type: NotificationType.Error });
      });
  };

  if (user) return <Navigate to="/" />;

  return (
    <Container size="xs">
      <Paper radius="md" p="xl" m="xl" withBorder>
        {notification && (
          <Notification
            type={notification.type}
            title={notification.title}
            onClose={() => setNotification(null)}
          >
            {notification.content}
          </Notification>
        )}

        <Text size="lg" weight={500}>
          Welcome to Clink, {type} with
        </Text>

        <Group grow mb="md" mt="md">
          <LinkedInButton radius="xl">LinkedIn</LinkedInButton>
          <GoogleButton radius="xl">Google</GoogleButton>
        </Group>

        <Divider
          label="Or continue with email"
          labelPosition="center"
          my="lg"
        />

        <form onSubmit={form.onSubmit(type === "register" ? register : login)}>
          <Stack>
            {type === "register" && (
              <Group grow>
                <TextInput
                  required
                  placeholder="First name"
                  {...form.getInputProps("firstName")}
                />
                <TextInput
                  required
                  placeholder="Last name"
                  {...form.getInputProps("lastName")}
                />
              </Group>
            )}

            <TextInput
              required
              placeholder="Your email"
              type="email"
              {...form.getInputProps("email")}
            />

            <PasswordInput
              required
              type="password"
              placeholder="Your password"
              {...form.getInputProps("password")}
            />

            {type === "login" && (
              <Anchor
                component="button"
                type="button"
                color="dimmed"
                onClick={() => {
                  navigate("/account/forgot");
                }}
                size="xs"
                align="left"
              >
                Forgot password?
              </Anchor>
            )}

            {type === "register" && (
              <>
                <PasswordInput
                  required
                  type="password"
                  placeholder="Repeat password"
                  {...form.getInputProps("confirmPassword")}
                />

                <Text size="xs" color="dimmed">
                  By clicking Register, you agree to our Terms. Learn how we
                  collect, use and share your data in our Privacy Policy and how
                  we use cookies and similar technology in our Cookies Policy.
                </Text>
              </>
            )}
          </Stack>

          <Group position="apart" mt="xl">
            <Anchor
              component="button"
              type="button"
              color="dimmed"
              onClick={() => {
                type === "register"
                  ? navigate("/account/login")
                  : navigate("/account/register");
                toggle();
              }}
              size="xs"
            >
              {type === "register"
                ? "Already have an account? Login"
                : "Don't have an account? Register"}
            </Anchor>
            <Button type="submit">{upperFirst(type)}</Button>
          </Group>
        </form>
      </Paper>
    </Container>
  );
}
