import {
  Button,
  Collapse,
  Grid,
  Group,
  Paper,
  Text,
  Title,
} from "@mantine/core";
import { IconEye, IconLink, IconPencil, IconWorld } from "@tabler/icons";
import { useAtom } from "jotai";
import { useEffect, useState } from "react";

import CardCreateLink from "../../components/links/CardCreateLink";
import CardLink from "../../components/links/CardLink";
import PersonalInformation from "../../components/PersonalInformation";

import { useNavigate } from "react-router-dom";
import useProfileService from "../../api/services/useProfileService";
import ProfileSkeleton from "../../components/loaders/ProfileSkeleton";
import ProfileAbout from "../../components/ProfileAbout";
import { profile as profileAtom } from "../../store/profile";

export default function PrivateProfile() {
  const [profile] = useAtom(profileAtom);
  const { getPrivateProfile } = useProfileService();
  const [isAboutExpanded, setIsAboutExpanded] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    getPrivateProfile();
  }, []);

  if (!profile || !profile.id) {
    return <ProfileSkeleton />;
  }

  return (
    <>
      <PersonalInformation profile={profile} editable />

      <Group position="apart" my={"sm"}>
        <Button
          size="xs"
          variant="light"
          onClick={() => navigate(`/profile/${profile?.account.id}`)}
          leftIcon={<IconEye size={16} />}
        >
          Preview
        </Button>

        <Button
          size="xs"
          variant="light"
          onClick={() => navigate(`/settings`)}
          leftIcon={<IconPencil size={16} />}
        >
          Profile Settings
        </Button>
      </Group>

      <ProfileAbout mt={"md"} mb="xl">
        {profile?.description}
      </ProfileAbout>

      <Group position="apart" mt="xl">
        <Title order={1} size="h2">
          Links
        </Title>
        <Button
          leftIcon={<IconLink size={16} />}
          size="sm"
          onClick={() => setIsAboutExpanded(!isAboutExpanded)}
        >
          Add link
        </Button>
      </Group>

      <Collapse in={isAboutExpanded} sx={{ width: "100%" }}>
        <CardCreateLink onFinish={() => setIsAboutExpanded(false)} />
      </Collapse>

      <Paper radius="md" my="md">
        <Grid mt="sm">
          {profile?.links.length ? (
            profile.links.map((link) => (
              <Grid.Col xs={12} key={link.platform}>
                <CardLink link={link} Icon={IconWorld} editable />
              </Grid.Col>
            ))
          ) : (
            <Grid.Col xs={12} mt="md" px="lg">
              <Text align="center" color="dimmed" weight={500}>
                You haven't added any links or numbers. Start adding so people
                can find and connect with you.
              </Text>
            </Grid.Col>
          )}
        </Grid>
      </Paper>
    </>
  );
}
