import { useState } from "react";
import { NavLink } from "react-router-dom";

import { Container, createStyles, Group, Header } from "@mantine/core";
import { IconGlassFull } from "@tabler/icons";
import { useAtom } from "jotai";
import useAccountService from "../../api/services/useAccountService";
import { user as userAtom } from "../../store/account";
import { Role } from "../../types/role";

const useStyles = createStyles((theme) => ({
  header: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    height: "100%",
  },

  links: {
    [theme.fn.smallerThan("xs")]: {
      display: "none",
    },
  },

  burger: {
    [theme.fn.largerThan("xs")]: {
      display: "none",
    },
  },

  link: {
    display: "block",
    lineHeight: 1,
    padding: "8px 12px",
    borderRadius: theme.radius.sm,
    textDecoration: "none",
    color:
      theme.colorScheme === "dark"
        ? theme.colors.dark[0]
        : theme.colors.gray[7],
    fontSize: theme.fontSizes.sm,
    fontWeight: 500,

    "&:hover": {
      backgroundColor:
        theme.colorScheme === "dark"
          ? theme.colors.dark[6]
          : theme.colors.gray[0],
    },
  },

  linkActive: {
    "&, &:hover": {
      backgroundColor: theme.fn.variant({
        variant: "light",
        color: theme.primaryColor,
      }).background,
      color: theme.fn.variant({ variant: "light", color: theme.primaryColor })
        .color,
    },
  },
}));

function Nav() {
  const [user] = useAtom(userAtom);

  const accountService = useAccountService();

  // TODO: Implement
  // Change to https://ui.mantine.dev/component/header-tabs
  const links = [
    { label: "Home", link: "/" },
    { label: "Profile", link: "/profile" },
    { label: "Settings", link: "/settings" },
    { label: "Admin", link: "/admin", Role: Role.Admin },
    { label: "Logout", action: accountService.logout },
  ];

  const [active, setActive] = useState(links[0].link);
  const { classes, cx } = useStyles();

  // const { colorScheme, toggleColorScheme } = useMantineColorScheme();
  // const theme = useMantineTheme();

  // only show nav when logged in
  // if (!user) return null;

  return (
    <Header height={60}>
      <Container className={classes.header}>
        <IconGlassFull size={28} />
        <Group spacing={5} className={classes.links}>
          {/* <Switch
            checked={colorScheme === "dark"}
            onChange={() => toggleColorScheme()}
            size="lg"
            onLabel={<IconSun color={theme.white} size={20} stroke={1.5} />}
            offLabel={
              <IconMoonStars
                color={theme.colors.gray[6]}
                size={20}
                stroke={1.5}
              />
            }
          /> */}
          <NavLink
            to="/"
            onClick={() => {
              setActive("/");
            }}
            className={cx(classes.link, {
              [classes.linkActive]: active === "/",
            })}
          >
            Dashboard
          </NavLink>
          <NavLink
            to="/profile"
            onClick={() => {
              setActive("/profile");
            }}
            className={cx(classes.link, {
              [classes.linkActive]: active === "/profile",
            })}
          >
            Profile
          </NavLink>
          <NavLink
            to="/profile/share"
            onClick={() => {
              setActive("/profile/share");
            }}
            className={cx(classes.link, {
              [classes.linkActive]: active === "/profile/share",
            })}
          >
            Share QR
          </NavLink>
          <NavLink
            to="/settings"
            onClick={() => {
              setActive("/settings");
            }}
            className={cx(classes.link, {
              [classes.linkActive]: active === "/settings",
            })}
          >
            Settings
          </NavLink>
          {user?.role === Role.Admin && (
            <NavLink
              to="/admin"
              onClick={() => {
                setActive("/admin");
              }}
              className={cx(classes.link, {
                [classes.linkActive]: active === "/admin",
              })}
            >
              Admin
            </NavLink>
          )}
        </Group>
      </Container>
    </Header>
    // <Route path="/admin" component={AdminNav} />
  );
}

export default Nav;
