import { Avatar, Group, Select, Text } from "@mantine/core";
import {
  IconBrandFacebook,
  IconBrandGithub,
  IconBrandInstagram,
  IconBrandLinkedin,
  IconBrandPinterest,
  IconBrandReddit,
  IconBrandSnapchat,
  IconBrandStackoverflow,
  IconBrandTelegram,
  IconBrandTiktok,
  IconBrandTumblr,
  IconBrandTwitter,
  IconBrandWhatsapp,
  IconBrandXing,
  IconBrandYoutube,
  IconDeviceMobile,
  IconLink,
  IconPhone,
  IconQuestionMark,
  IconWorld,
} from "@tabler/icons";
import { forwardRef } from "react";
import { LinkType } from "../../types/linkType";

const LinkTypeSelect = ({ formInputProps, withAsterisk }) => {
  const data = [
    {
      icon: <IconWorld />,
      label: "Website",
      value: LinkType.Website,
      description: "Personal or business website, portfolio, blog...",
    },
    {
      icon: <IconLink />,
      label: "Custom Link",
      value: LinkType.CustomLink,
      description: "Add a custom link.",
    },
    {
      icon: <IconDeviceMobile />,
      label: "Mobile Phone",
      value: LinkType.Mobile,
      description: "Specifically for mobile phones.",
    },
    {
      icon: <IconPhone />,
      label: "Personal Phone",
      value: LinkType.PersonalPhone,
      description: "",
    },
    {
      icon: <IconPhone />,
      label: "Business Phone",
      value: LinkType.BusinessPhone,
      description: "",
    },
    {
      icon: <IconBrandLinkedin />,
      label: "LinkedIn",
      value: LinkType.LinkedIn,
      description: "",
    },
    {
      icon: <IconBrandTwitter />,
      label: "Twitter",
      value: LinkType.Twitter,
      description: "",
    },
    {
      icon: <IconBrandFacebook />,
      label: "Facebook",
      value: LinkType.Facebook,
      description: "",
    },
    {
      icon: <IconBrandInstagram />,
      label: "Instagram",
      value: LinkType.Instagram,
      description: "",
    },
    {
      icon: <IconBrandWhatsapp />,
      label: "WhatsApp",
      value: LinkType.WhatsApp,
      description: "",
    },
    {
      icon: <IconBrandTelegram />,
      label: "Telegram",
      value: LinkType.Telegram,
      description: "",
    },
    {
      icon: <IconBrandXing />,
      label: "Xing",
      value: LinkType.Xing,
      description: "",
    },
    {
      icon: <IconBrandYoutube />,
      label: "YouTube",
      value: LinkType.YouTube,
      description: "",
    },
    {
      icon: <IconBrandTiktok />,
      label: "TikTok",
      value: LinkType.TikTok,
      description: "",
    },
    {
      icon: <IconBrandSnapchat />,
      label: "Snapchat",
      value: LinkType.Snapchat,
      description: "",
    },
    {
      icon: <IconBrandPinterest />,
      label: "Pinterest",
      value: LinkType.Pinterest,
      description: "",
    },
    {
      icon: <IconBrandReddit />,
      label: "Reddit",
      value: LinkType.Reddit,
      description: "",
    },
    {
      icon: <IconBrandStackoverflow />,
      label: "StackOverflow",
      value: LinkType.StackOverflow,
      description: "",
    },
    {
      icon: <IconBrandGithub />,
      label: "GitHub",
      value: LinkType.GitHub,
      description: "",
    },
    {
      icon: <IconBrandTumblr />,
      label: "Tumblr",
      value: LinkType.Tumblr,
      description: "",
    },
    {
      icon: <IconQuestionMark />,
      label: "[TEST] Indeed",
      value: LinkType.Indeed,
      description: "",
    },
    {
      icon: <IconQuestionMark />,
      label: "[TEST] Signal",
      value: LinkType.Signal,
      description: "",
    },
    {
      icon: <IconQuestionMark />,
      label: "[TEST] WeChat",
      value: LinkType.WeChat,
      description: "",
    },
  ];

  return (
    <Select
      withAsterisk={withAsterisk}
      dropdownPosition="bottom"
      label="Link Type"
      placeholder="LinkedIn, Facebook, Twitter..."
      itemComponent={LinkTypeSelectItem}
      data={data}
      searchable
      maxDropdownHeight={200}
      nothingFound="No luck... Perhaps custom link can do the work?"
      filter={(value, item) =>
        item.label.toLowerCase().includes(value.toLowerCase().trim()) ||
        item.description.toLowerCase().includes(value.toLowerCase().trim())
      }
      {...formInputProps}
    />
  );
};

const LinkTypeSelectItem = forwardRef(
  ({ icon, label, description, ...others }, ref) => (
    <div ref={ref} {...others}>
      <Group noWrap>
        <Avatar>{icon}</Avatar>

        <div>
          <Text size="sm">{label}</Text>
          <Text size="xs" opacity={0.65}>
            {description}
          </Text>
        </div>
      </Group>
    </div>
  )
);

export default LinkTypeSelect;
