import React, { useState } from "react";

import {
  ActionIcon,
  Anchor,
  Avatar,
  Collapse,
  createStyles,
  Group,
  Text,
} from "@mantine/core";
import { IconChevronUp, IconEyeOff, IconPencil } from "@tabler/icons";
import CardEditLink from "./CardEditLink";

const useStyles = createStyles((theme) => ({
  cardLink: {
    "&:hover": {
      textDecoration: "none",
    },
  },
}));

const CardLink = ({ link, Icon, editable = false }) => {
  const { classes, theme } = useStyles();
  const { url, active } = link;

  const [isEditMode, setIsEditMode] = useState(false);

  const content = (
    <>
      <Group noWrap position="apart">
        <Group noWrap position="left">
          {active ? (
            <Avatar radius="xl" color={theme.fn.primaryColor()}>
              <Icon size={22} />
            </Avatar>
          ) : (
            <Avatar radius="xl" color="gray">
              <IconEyeOff size={22} color="gray" />
            </Avatar>
          )}
          <Text size="sm" italic lineClamp={1}>
            {url}
          </Text>
        </Group>

        {editable && (
          <ActionIcon size="lg" onClick={() => setIsEditMode(!isEditMode)}>
            {!isEditMode ? (
              <IconPencil size={22} />
            ) : (
              <IconChevronUp size={22} />
            )}
          </ActionIcon>
        )}
      </Group>

      {editable && (
        <Collapse in={isEditMode} mt="xs" sx={{ width: "100%" }}>
          <CardEditLink link={link} onCancel={() => setIsEditMode(false)} />
        </Collapse>
      )}
    </>
  );

  if (editable) {
    return content;
  } else {
    return (
      <Anchor href={url} target="_blank" className={classes.cardLink}>
        {content}
      </Anchor>
    );
  }
};

export default CardLink;
